$alto-tarjeta: 100px;
$alto-header : 34px;
.pay-content {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
  position: relative;
  top: 0;
  border: 1px solid rgba(black, .1);

  .client {
    text-align: center;
    display: block;
    padding: 2.5px 0;
    a {
      text-decoration: none;
      color: $primary;
    }
  }

  .buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5px 0;
    .MuiIconButton-root {
      padding: 2.5px !important;
      color: $primary !important;
      .MuiSvgIcon-root {
        width: 1.2rem;
        height: 1.2rem;
      }
    }
  }

  &.pendiente {
    .buttons {
      border-bottom: 2.5px solid $gray-300;
    }
    .pay-header small{
      background: $gray-300;
    }
  }

  &.cobrado {
    .buttons {
      border-bottom: 2.5px solid $green;
    }
    .pay-header small{
      background: $green;
      color: white;
    }
  }

  &.mora {
    .buttons {
      border-bottom: 2.5px solid $red;
    }
    .pay-header small{
      background: $red;
      color: white;
    }
  }
}

.pay-header {
  position: relative;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: $alto-header;
  small {
    background: rgba($primary, .1);
    color: $gray-600;
    padding: 2px 5px;
  }
  .dollar {
    font-size: 1.2em;
    font-weight: 500;
    border-radius: 10px;
    padding: 5px .5rem;
    &.pendiente {
      color: $gray-700;
    }
    &.mora {
      color: $red;
    }
    &.cobrado {
      color: $green;
    }
  }
}

.st {
  &.pendiente {
    color: $gray-700;
  }
  &.mora {
    color: $red;
  }
  &.cobrado {
    color: $green;
  }
  font-weight: 500;
}
