.content-google-map {
  width: 100%;
  position: relative;
}
.map-btn {
  position: absolute;
  background: rgba(white, .6);
  bottom: 32px;
  padding: 2px 2.5px;
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
  left: 0;
  display: flex;
  flex-direction: column;
}
//map
.google-map {
  position: relative !important;
  width: 100% !important;
  min-height: 450px !important;
}

.google-map-min {
  position: relative !important;
  width: 100% !important;
}

.map-dialog {
  width: 100%;
  @include desktop {
    width: 800px;
  }
}

.dialog-map-header {
  h2 {
    margin: 0;
  }
}

//end map
